<template>
	<YPage :logo="require('@/assets/img/logo_white.png')">
		<div>
			<YMenuTab :list="tabList" :current="current" @change="handleTabChange"/>
			<div class="yu-page-container s-markdown-index">
				<YMarkdownPanel
					v-for="(item,index) in markdownList" :key="index"
					:url="'/markdown/detail?name=' + item.title"
					:year="getDatetimeValue(item.datetime,0)"
					:month="getDatetimeValue(item.datetime,1)"
					:day="getDatetimeValue(item.datetime,2)"
					:title="item.title" 
					:desc="item.desc"
					:tags="item.tags"/>
			</div>
		</div>
	</YPage>
</template>

<script>
	import YMenuTab from "@/components/YMenuTab"
	import YMarkdownPanel from "@/components/YMarkdownPanel"
	import mdMenu from "@/mock/mdMenu"
	import markdownMock from "@/mock/markdownList"
	export default {
		components: {
			YMenuTab,YMarkdownPanel
		},
		data() {
			return {
				tabList:[],
				current:0,
				markdownList:[]
			}
		},
		mounted() {
			this.tabList = mdMenu;
			this.markdownList = markdownMock[mdMenu[this.current].id];
			console.log(this.markdownList)
		},
		methods:{
			handleTabChange(index){
				this.markdownList = markdownMock[mdMenu[index].id];
			},
			getDatetimeValue(datetime,index = 0){
				if(!datetime) return '无'
				let arr = datetime.split('-');
				return arr[index]
			}
		}
	}
</script>

<style lang="scss" scoped>
	.s-markdown-index{
		padding-bottom: 10px;
	}
</style>