<template>
	<div class="yu-menu-tab-wrapper">
		<div class="yu-menu-tab">
			<div class="yu-menu-tab-list" :class="{'yu-menu-tab-list-current':currentTab === index}" v-for="(item,index) in list" :key="item.id"  @click="handleSwitchTab(index)">
				<div class="name" v-text="item.name"></div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'YMenuTab',
		props: {
			list: {
				type: Array,
				default () {
					return []
				}
			},
			current: {
				type: Number,
				default () {
					return 0
				}
			},
		},
		data() {
			return {
				currentTab:0
			}
		},
		watch: {
			'current':{
				handler: function(newVal) {
					if (newVal) {
						this.currentTab = newVal;
					}
				},
				immediate: true
			}
		},
		methods: {
			handleSwitchTab(index){
				this.currentTab = index;
				this.$emit('change',index)
			}
		}
	}
</script>
<style lang="scss" scoped>
	.yu-menu-tab-wrapper{
		background-color: #fff;
		.yu-menu-tab{
			width: 1080px;
			padding: 10px 0;
			margin: 0 auto;
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			.yu-menu-tab-list{
				margin: 0 10px;
				padding: 0 10px;
				.name{
					position: relative;
					padding:5px 0;
					font-size: 15px;
					line-height: 32px;
					font-weight: 500;
					color: #999;
					cursor: pointer;
					&::after{
						content: "";
						position: absolute;
						left: 50%;
						bottom: 0;
						transform: translateX(-50%);
						width: 30px;
						height: 3px;
						border-radius: 10px;
						background-color: transparent;
					}
				}
			}
			.yu-menu-tab-list-current{
				.name{
					color: #314e98;
					&::after{
						background-color: #314e98;
					}
				}
			}
		}
	}
</style>