<template>
	<div class="yu-panel-wrapper" @click="handleClick">
		<div class="yu-panel-hd">
			<div class="time" v-text="`${month} · ${day}`"></div>
			<div class="year" v-text="year"></div>
			<div class="divider"></div>
		</div>
		<div class="yu-panel-bd">
			<div class="title" v-text="title"></div>
			<div class="desc" v-text="desc"></div>
			<div class="tags" v-if="tags && tags.length > 0">
				<span v-for="(item,index) in tags" :key="index" v-text="item" :style="{backgroundColor:createBgcolor(index)}"></span>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'YMarkdownPanel',
		props: {
			title: {
				type: String,
				default () {
					return ''
				}
			},
			desc: {
				type: String,
				default () {
					return ''
				}
			},
			url: {
				type: String,
				default () {
					return ''
				}
			},
			year: {
				type: [Number,String],
				default () {
					return ''
				}
			},
			month: {
				type: [Number,String],
				default () {
					return ''
				}
			},
			day: {
				type: [Number,String],
				default () {
					return ''
				}
			},
			tags: {
				type: Array,
				default () {
					return []
				}
			},
		},
		data() {
			return {
				
			}
		},
		watch: {
			
		},
		methods: {
			createBgcolor(index){
				let arr = ['#4f78f6','#4fbd98','#e2b135','#ff5e5e','#66b2ff'];
				return arr[index] || '#4f78f6'
			},
			handleClick(){
				this.$router.push(this.url)
			}
		}
	}
</script>
<style lang="scss" scoped>
	.yu-panel-wrapper{
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		cursor: pointer;
		background-color: #fff;
		border-radius: 8px;
		padding: 20px 30px;
		margin: 12px 0;
		.yu-panel-hd{
			padding-right: 30px;
			.time{
				font-size: 17px;
				line-height: 24px;
				font-weight: 600;
			}
			.year{
				font-size: 12px;
				line-height: 18px;
				color: #999;
			}
			.divider{
				margin-top: 20px;
				background-color: #e6e6e6;
				height: 1px;
			}
		}
		.yu-panel-bd{
			-webkit-flex: 1;
			-ms-flex: 1;
			-webkit-box-flex: 1;
			-moz-box-flex: 1;
			flex: 1;
			.title{
				font-size: 15px;
				line-height: 24px;
				font-weight: 600;
			}
			.desc{
				padding: 2px 0;
				font-size: 12px;
				line-height: 20px;
				color: #999;
			}
			.tags{
				font-size: 0;
				margin-top: 4px;
				margin-bottom: -6px;
				span{
					display: inline-block;
					font-size: 12px;
					line-height: 20px;
					padding: 0 6px;
					margin-right: 6px;
					margin-bottom: 6px;
					border-radius: 3px;
					color: #fff;
					vertical-align: middle;
					// text-transform: uppercase;
				}
			}
		}
	}
</style>